  // 
  async function checkLotteryStatus() {
    try {
      const readContract = await getReadOnlyContract();
      const isOpen = await readContract.isOpenLottery();
      setIsOpenLottery(isOpen);
      
      const stopTime = await readContract.getStopTime();
      setStopTime(stopTime);
      
      const currentTime = Math.floor(Date.now() / 1000);
      const timeLeft = Number(stopTime) - currentTime;
      
      console.log(":");
      console.log("- :", isOpen);
      console.log("- :", new Date(Number(stopTime) * 1000).toLocaleString());
      console.log("- :", timeLeft > 0 ? `${Math.floor(timeLeft / 3600)}${Math.floor((timeLeft % 3600) / 60)}` : "");
      
      return { isOpen, stopTime, timeLeft };
    } catch (error) {
      console.error(":", error);
      return null;
    }
  }

