  // 
  const filteredHistory = () => {
    if (!myTickets || myTickets.length === 0) return [];
    if (historyFilter === 'all') return myTickets;
    return myTickets.filter(item => item.status === historyFilter);
  };

  // 
  const formatIndex = (tickets) => {
    if (!tickets || tickets.length === 0) return "-";
    const indexes = tickets.map(ticket => `#${ticket.index}`);
    if (indexes.length <= 2) {
      return indexes.join(', ');
    } else {
      // 2
      return `${indexes.slice(0, 2).join(', ')} ${t("ticketHistory.andMore", { count: indexes.length - 2 })}`;
    }
  };

  // 
  const createTicketTooltip = (tickets) => {
    if (!tickets || tickets.length === 0) return "";
    
    const indexes = tickets.map(ticket => `#${ticket.index}`);
    if (indexes.length <= 5) {
      return indexes.join(', ');
    } else {
      // 5
      return `${indexes.slice(0, 5).join(', ')} ${t("ticketHistory.andMore", { count: indexes.length - 5 })}`;
    }
  };

  // 
  const formatDateString = (dateString) => {
    if (!dateString) return "-";
    const date = new Date(dateString);
    return date.toLocaleString();
  };

